import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpHeaders,
  HttpEventType,
  HttpErrorResponse,
  HttpResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { AuthService } from 'src/app/services/auth.service';
import { NotificationService } from '../services/notification.service';
import { loader } from '../app.component';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {

  constructor(private auth: AuthService, private notifier: NotificationService) { }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<any> {
    loader.of.next(true);
    const token = this.auth.appUser.token;

    request = request.clone({
      headers: new HttpHeaders({
        Accept: 'application/json',
        'Content-Type': 'application/json',
        Authorization: `Bearer ${ token }`,
      })
    });

    return next.handle(request).pipe(
      catchError(err => {
        // console.log(err);
        // authentication error
        if (err.status === 401)
        {
          this.notifier.error(err.statusText);
          return throwError(err.statusText);
        }

        // Handle application error
        if (err instanceof HttpErrorResponse)
        {
          if (err.error)
          {
            let message = '';
            Object
              .keys(err.error)
              .forEach(k => {
                const error = err.error[k];
                if (Array.isArray(error))
                {
                  error.forEach(e => message += `${ k }-${ e }\n`);
                }
                else
                {
                  message += `${ k }-${ error }\n`;
                }
              });

            this.notifier.error(message);
            return throwError(message);
          }

          if (err.headers.get('Message') || err.headers.get('message'))
          {
            console.log(err.headers.get('Message'));
            const error = err.headers.get('message') || err.headers.get('Message');
            if (error)
            {
              this.notifier.error(error);
              return throwError(error);
            }
          }
        }

        if (err.error.message)
        {
          this.notifier.error(err.error.message);
          return throwError(err.error.message);
        }

        // Else
        throwError(err);
      }),
      map(e => {
        loader.of.next(false);
        if (e instanceof HttpResponse && e.status === 200)
        {
          const message = e.headers.get('Message') || e.headers.get('message');
          if (message && message.length > 0)
          {
            this.notifier.success(message);
          }
        }
        return e;
      }));
  }
}
