import { Component, OnInit, Input } from '@angular/core';
import { Resource } from '../resource.model';
import { NotificationService } from 'src/app/services/notification.service';
import { Router, ActivatedRoute } from '@angular/router';

export interface Action {
  icon: string;
  type: 'url' | 'callback';
  url?: string;
  callback?: (e: Resource) => void;
}

export interface ITableContext<T> {
  urls: {
    createUrl: string;
    detailsUrl: string;
    editUrl?: string;
    deleteUrl?: string;
  };
  title: string;
  data: T[];
  properties: string[];
  actions?: Action[];
}

@Component({
  selector: 'app-table-view',
  templateUrl: './table-view.component.html',
  styleUrls: ['./table-view.component.scss']
})
export class TableViewComponent<TModel> implements OnInit {

  @Input() context: ITableContext<TModel>;

  constructor(private notifier: NotificationService, private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    if (!this.context)
    {
      this.notifier.error('Something went wrong, Table context is null');
      // Default context
      this.context = {
        urls: {
          createUrl: '<>',
          detailsUrl: '<>'
        },
        title: 'List',
        data: [],
        properties: []
      };
    }

    if (!this.context.actions)
    {
      this.context.actions = [];
    }
    this.context.actions.push({
      // icon: 'fa fa-info-circle',
      icon: 'nc-icon nc-alert-circle-i',
      type: 'url',
      url: this.context.urls.editUrl || ':id/details'
    });
    this.context.actions.push({
      // icon: 'fa fa-edit',
      icon: 'nc-icon nc-tag-content',
      type: 'url',
      url: this.context.urls.editUrl || ':id/edit'
    });
    this.context.actions.push({
      // icon: 'fa fa-trash-o',
      icon: 'nc-icon nc-simple-remove',
      type: 'url',
      url: this.context.urls.deleteUrl || ':id/delete'
    });
  }


  public do(e: TModel, a: Action): void {
    switch (a.type)
    {
      case 'callback':
        a.callback(e);
        break;
      case 'url':
        // tslint:disable-next-line: no-string-literal
        this.router.navigate([a.url.replace(':id', e['id'])], { relativeTo: this.route, state: { resource: e }, });
        break;
    }
  }
}
